import FieldsValidationConfig from "./Validation/FieldsValidationConfig";
import FormValidationConfig from "./Validation/FormValidationConfig";
import CollectionValidationConfig from "./Validation/CollectionValidationConfig";

/**
 * Validation config should configure 3 areas:
 * ##1 fieldContext - ``(fields rules)``:
 *      *required for offline validation*
 *       **required** - czy jest niepuste
 *       **type consistency** - czy zgadza si typ wartoci
 *       **value correctness** - czy warto jest poprawna
 *  ##2 itemContext - ``(how field depend on other fields in record)``
 *      *required for offline validation*
 *      np. isPartnership determinuje czy partnership jest required
 *      *Waciwie to ten przykad nie musi by walidowany, poniewa zaatwia go zachowanie kontrolek*
 *      *- Jeli isPartnership to partnership jest wczony i wymagany*
 *      *- Jeli nie isPartnership to partnership jest wyczony i nawet nie trafia do walidacji*
 *  ##3 collectionContext - ``(how fields depend on other items fields)``
 *     *required for any validation*
 *      np. __*isDefault*__ jest unikalne w kontekcie kolekcji, tzn. moe by tru tylko w jednym rekordzie
 *      np. __*bankAccountName*__ jest unikalne w kontekcie kolekcji, tzn. nie moe si powtarza w innych rekordach
 *      np. __*accountNumber*__ jest unikalne w kontekcie kolekcji, tzn. nie moe si powtarza w innych rekordach
 * @property {FieldsValidationConfig} fieldsRules - zasady walidacji pl
 * @property {FormValidationConfig} formRules - zasady walidacji pl w kontekcie formularza
 * @property {CollectionValidationConfig} collectionRules - zasady walidacji pl w kontekcie kolekcji
 */
export default class ValidationConfig {
    
    /**
     * Constructor of ValidationConfig
     *
     * @param {[]} [arguments] - role walidacji zdefiniowane klasami strategii
     */
    constructor() {
        // console.log('ValidationConfig.arguments', arguments);
        for (let rules of arguments) {
            if (rules instanceof FieldsValidationConfig) {
                //zasady walidacji pl
                this.fieldsRules = rules;
            } else if (rules instanceof FormValidationConfig) {
                //zasady walidacji pl w kontekcie formularza
                this.formRules = rules;
            } else if (rules instanceof CollectionValidationConfig) {
                //zasady walidacji pl w kontekcie kolekcji
                this.collectionRules = rules;
            }
        }
    }
    
}