/**
 * @name CollectionValidationConfig
 *
 * #Konfiguracja walidacji w kontekcie kolekcji
 * ##Role walidacji:
 * - unique - see {@link CollectionUniqueRule}
 *
 * @property {Object} [...arguments] - typ pola role validacji zdefiniowane obiektami klas rl
 *
 * @description **Warunki walidacji dla formularza:**
 * Kady argument przekazany do konstruktora klasy powinien by instancj klasy roli
 * see: ({@link /App/Api/Component/ValidationRules/} folder)
 */
export default class CollectionValidationConfig {
    rules = [];
    
    /**
     * Konfiguracja walidacji pola w kontekcie formularza
     *
     * @param {[]} [arguments] - role validacji zdefiniowane obiektami klas rl
     * @see CollectionUniqueRule
     */
    constructor() {
        // console.log('arguments', arguments);
        
        //naley uwzgldni dowoln ilo argumentw/rl
        for (let rule of arguments) {
            this.rules.push(rule);
        }
    }
}